.container {
  display: flex;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
  justify-content: center;
}

.flex-row {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.time-container {
  font-weight: 700;
  font-size: 18px;
  align-self: center;
  text-align: center;
  padding: 5px;
}

.number-container {
  margin-top: 5px;
  padding: 20px;
  border: 1px solid black;
}

.title {
  font-family: 'Clicker Script', cursive;
  font-weight: 400;
  font-size: 72px;
}